@import url("https://fonts.googleapis.com/css?family=PT+Sans:400,700&subset=cyrillic");
@import url("https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300&subset=cyrillic");
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-basket {
  width: 28px;
  height: 21px;
  background-image: url("../img/sprite.png");
  background-position: -107px -48px;
}

.icon-exel {
  width: 97px;
  height: 111px;
  background-image: url("../img/sprite.png");
  background-position: 0px 0px;
}

.icon-logo-footer {
  width: 36px;
  height: 38px;
  background-image: url("../img/sprite.png");
  background-position: -107px 0px;
}

body,
html {
  font-size: 14px;
  font-family: "PT Sans";
  font-weight: bold;
  height: 100%;
  min-height: 100%;
}

.out {
  height: 100%;
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.inner {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 10px;
  width: 100%;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.content__container {
  overflow: hidden;
  width: auto;
  display: block;
}

.content .items {
  margin-bottom: 20px;
  font-size: 0;
  margin-left: -7px;
  margin-right: -7px;
  padding-left: 20px;
}

.content .item {
  display: inline-block;
  vertical-align: top;
  width: 33.3333%;
  padding: 10px 7px;
}

.content .item__img img {
  max-width: 100%;
  height: auto;
}

.content .item__inner {
  background-color: #dcdcdc;
  padding: 20px;
}

.content .item__text {
  font-size: 18px;
  color: #767676;
  font-weight: 400;
  font-style: normal;
  margin-top: 20px;
}

.content .item__controls {
  margin-top: 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.content .item__price {
  font-size: 28px;
  color: #444444;
  font-weight: 700;
}

.content .item__basket {
  margin-right: 18px;
  text-decoration: none;
  cursor: pointer;
}

.content .item__basket-icon {
  display: block;
}

.content .text {
  font-size: 0;
  margin-bottom: 200px;
}

.content .text__row {
  margin-left: -20px;
  margin-right: -20px;
}

.content .text__col {
  width: 50%;
  display: inline-block;
  vertical-align: top;
  font-size: 16px;
  padding: 0 20px;
}

.content .text__col p {
  color: #767676;
  font-size: 14px;
  line-height: 1.57;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.header {
  background-color: #dcdcdc;
  padding: 45px 0 65px;
}

.header__logo {
  display: inline-block;
  font-size: 0;
  max-width: 310px;
  margin-top: 20px;
}

.header__logo:after {
  content: " ";
  display: table;
  clear: both;
}

.header__logo-icon {
  float: left;
  width: 100px;
  height: 100px;
  margin-right: 13px;
  background-color: #777777;
}

.header__title, .header__meta {
  color: #777777;
  line-height: 22px;
  display: inline-block;
  vertical-align: top;
}

.header__title {
  font-size: 28px;
  margin-top: 8px;
}

.header__meta {
  font-size: 18px;
  margin-top: 20px;
}

.header__info {
  float: right;
}

.header__links {
  text-align: right;
}

.header__link {
  display: block;
  font-size: 40px;
  font-weight: 100;
  text-decoration: none;
  color: #767676;
  line-height: 1.15;
  font-family: "Open Sans Condensed";
  cursor: pointer;
}

.header__link_feedback {
  border-bottom: 2px dashed #777777;
  font-size: 24px;
  font-family: "PT Sans";
  display: inline-block;
  font-weight: 700;
  line-height: 1;
  margin-top: 10px;
}

.sidebar {
  display: inline-block;
  height: 100%;
  min-height: 100%;
  float: left;
}

.sidebar__content {
  font-size: 0;
}

.sidebar_logo {
  font-size: 0;
  text-align: center;
  margin-top: 110px;
}

.sidebar_logo-icon {
  display: block;
  margin: 0 auto;
}

.sidebar_logo-title {
  font-size: 30px;
  letter-spacing: 0.025em;
  line-height: 0.83;
  color: #444444;
  text-decoration: none;
  margin-top: 5px;
  display: block;
}

.menu_sidebar .menu__item {
  display: block;
}

.menu_sidebar .menu__item.is-active .menu__link {
  text-decoration: underline;
}

.menu_sidebar .menu__item.is-active .menu__submenu .menu__link {
  text-decoration: none;
}

.menu_sidebar .menu__item .menu__submenu {
  padding-left: 20px;
}

.menu_sidebar .menu__item .menu__submenu .menu__item {
  position: relative;
}

.menu_sidebar .menu__item .menu__submenu .menu__item:before {
  content: '';
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: -11px;
  width: 5px;
  height: 5px;
  background-color: #767676;
  border-radius: 50px;
}

.menu_sidebar .menu__item .menu__submenu .menu__item:not(:last-child) {
  margin-bottom: 5px;
}

.menu_sidebar .menu__item .menu__submenu .menu__link {
  color: #767676;
  font-size: 14px;
  transition: color .24s ease-in-out;
}

.menu_sidebar .menu__item .menu__submenu .menu__link.is-active,
.menu_sidebar .menu__item .menu__submenu .menu__link:hover {
  color: #000;
  text-decoration: underline;
}

.menu_sidebar .menu__link {
  display: block;
  text-decoration: none;
  font-size: 14px;
  line-height: 30px;
  letter-spacing: 0.025em;
  color: #767676;
}

.menu_sidebar .menu__link.is-active {
  text-decoration: underline;
}

.footer {
  padding: 50px 0;
  background-color: #777777;
}

.footer_logo {
  margin-left: auto;
  max-width: 220px;
}

.footer_logo:after {
  content: " ";
  display: table;
  clear: both;
}

.footer_logo-icon {
  margin-right: 10px;
  float: left;
}

.footer_logo-title {
  line-height: 1.375;
  font-size: 14px;
  color: #fff;
  display: inline-block;
  vertical-align: top;
  position: relative;
}

.footer_logo-title:after {
  content: '';
  position: absolute;
  top: 50%;
  right: -19px;
  transform: translateX(-50%);
  width: 9px;
  height: 1px;
  background-color: #fff;
}

.footer_logo-meta {
  color: currentColor;
  color: #fff;
  font-size: 14px;
  margin-left: 5px;
  display: block;
}

.footer__copy {
  font-size: 14px;
  color: #fff;
  line-height: 1.375;
  font-weight: 400;
}

.footer__copy a {
  color: #fff;
  text-decoration: none;
  display: block;
}

.footer__copy a:hover {
  text-decoration: underline;
}

.footer .col {
  width: 33.3333%;
  display: inline-block;
  vertical-align: top;
}

.footer .row {
  font-size: 0;
}

.menu {
  font-size: 0;
}

.menu__item {
  display: inline-block;
}

.menu__link {
  text-decoration: none;
}

.menu.menu_nav {
  background-color: #777777;
  text-align: center;
}

.menu.menu_nav .menu__item:not(:last-child) .menu__link:after {
  content: '';
  display: block;
  position: absolute;
  width: 3px;
  height: 25px;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  background-color: #fff;
}

.menu.menu_nav .menu__link {
  font-size: 30px;
  color: #fff;
  font-weight: 400;
  padding: 26px 20px;
  position: relative;
  display: inline-block;
  vertical-align: top;
  line-height: 1;
}

.menu.menu_nav .menu__link:hover {
  text-decoration: underline;
}

.menu.menu_footer .menu__item {
  display: block;
}

.menu.menu_footer .menu__item:not(:last-child) {
  margin-bottom: 10px;
}

.menu.menu_footer .menu__link {
  text-decoration: underline;
  font-size: 14px;
  color: #fff;
  font-weight: 400;
  position: relative;
  display: inline-block;
  vertical-align: top;
  line-height: 1;
}

.breadcrumbs {
  font-size: 0;
  padding: 10px 0;
  margin-bottom: 20px;
}

.breadcrumbs__item {
  display: inline-block;
}

.breadcrumbs__item:last-child .breadcrumbs__link {
  text-decoration: none;
}

.breadcrumbs__item:not(:last-child) {
  margin-right: 26px;
  position: relative;
}

.breadcrumbs__item:not(:last-child):after {
  content: '/';
  display: block;
  position: absolute;
  top: 50%;
  font-size: 14px;
  transform: translateY(-50%);
  right: -16px;
  color: #767676;
}

.breadcrumbs__link {
  text-decoration: underline;
  display: block;
  cursor: pointer;
  font-size: 14px;
  color: #767676;
  letter-spacing: 0.025em;
}

.pagination {
  padding-bottom: 65px;
  margin-bottom: 30px;
  border-bottom: 1px solid #767676;
  font-size: 0;
  text-align: center;
}

.pagination__item {
  display: inline-block;
  margin: 0 10px;
}

.pagination__link {
  text-decoration: none;
  cursor: pointer;
  font-size: 14px;
  color: #767676;
}

.pagination__link.is-current {
  font-size: 18px;
  color: #777777;
  font-weight: 400;
}
